import {Component, Input, Output, EventEmitter} from 'angular2/core';


@Component({
  selector: 'like',
  templateUrl: 'app///like/like.html',
  styleUrls: ['app///like/like.css'],
  providers: [],
  directives: [],
  pipes: []
})
export class Like {

  @Input('liked') liked: boolean = false;
  @Input('initialCount') likes: number = 30;

  @Output('likeChange') change = new EventEmitter();

  onClick() {
    this.liked = !this.liked;
    if(!this.liked) {this.likes --}
    if(this.liked) {this.likes ++}
      
    this.change.emit({ newValue: this.liked });
  }

}
